.myProjects{
    ul{
        width: 80%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        list-style: none;
    }
}
.projects{
    &__item{
        box-sizing: border-box;
        width: 37%;
        background: rgb(101, 0, 184);
        color: white;
        padding-bottom: 1.5em;
        h3{
            margin: 0.5em 0;
        }
        figure{
            margin: 0;
            position: relative;
            p{
                width: 90%;
                margin: 0 auto;
                position: relative;
            }
            p:before{
                content: '';
                position: absolute;
                top: -5%;
                right: -1.5%;
                bottom: -20%;
                left: -1.5%;
                border-top: solid 2px;
                border-bottom: solid 2px;
                transition: 0.3s;
                transform: scaleX(0);
            }
        }
        figure:before{
            content: '';
            position: absolute;
            top: 3.5%;
            right: 3.5%;
            bottom: -2%;
            left: 3.55%;
            border-top: solid 2px;
            transition: 0.3s;
            transform: scaleX(0);
        }
        figure:hover:before{
            transform: scale(1);
        }
        figure:after{
            content: '';
            position: absolute;
            top: 3.5%;
            right: 3.5%;
            bottom: -3%;
            left: 3.5%;
            border-left: solid 2px;
            border-right: solid 2px;
            transition: 0.3s;
            transform: scaleY(0);
        }
        figure:hover:after{
            transform: scale(1);
        }
        figure:hover{
            p:before{
                transform: scale(1);
            }

        }
        img{
            width: 20vw;
            height: 13vw;
            margin: 2em 0em 0em 0em;
        }
        a{
            position: sticky;
            z-index: 50;
            color: white;
            text-decoration: none;
        }
        a:hover{
            text-decoration: underline;
        }
    }
}

@media screen and (max-width: 992px) {
    .myProjects{
        ul{
            flex-direction: column-reverse;
            align-items: center;
        }
    }
    .projects{
        &__item{
            width: 80%;
            margin: 1em 1.5em 0em 0em;
            img{
                width: 50vw;
                height: 35vw;
            }
        }
    }
}